@import url('https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,700,900&display=swap');

* {
    font-family: "Poppins", sans-serif;
}

.container {
    position: absolute;
    width: 90%;
    height: calc(100vh - 128px);
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("p404.png"), #151729;
}

.container .content {
    max-width: 600px;
    text-align: center;
}

.container .content h2 {
    font-size: 16vw;
    color: #fff;
    line-height: 1em;
}

.container .content h4 {
    position: relative;
    font-size: 1.5em;
    margin-top: 20px;
    margin-bottom: 30px;
    color: #111;
    background: #fff;
    font-weight: 300;
    padding: 10px 20px;
    display: inline-block;
}

.container .content p {
    color: #fff;
    font-size: 1.2em;
}

.container .content a {
    position: relative;
    display: inline-block;
    padding: 10px 25px;
    background: #00ffc4;
    color: #111;
    text-decoration: none;
    margin-top: 30px;
    border-radius: 25px;
    border-bottom: 4px solid #00ddc4;
}